import { Component, OnInit } from '@angular/core';
import { TeleMarketingHttpService } from './http/telemarketing.http.service';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from '../../../../@uisftech/common/service/commonFun';
import * as $ from 'jquery';
import value from '*.json';
import * as _ from 'lodash';
import { ActivatedRoute, Router } from '@angular/router';
@Component({
    selector: 'telemarketing',
    templateUrl: './telemarketing.component.html',
    styleUrls: ['./telemarketing.component.scss'],
    providers: [TeleMarketingHttpService, Commfunc, ConfirmationService]
})
export class TeleMarketingComponent implements OnInit {
    show: number = 1;
    msgs: any[];
    custBasic: any = {};
    dataList: any = {};
    MarketingCampaigntType: any = [];
    code: any;
    voice_path = "";
    aconnectid = "";
    called: string;
    phoneNum: string;
    callid: string;
    userId: string;
    resultList: any = {};//返回的数据
    startTime: string;//开始时间
    endTime: string;//结束时间
    dateTime: string;
    urlParam: string;//路径
    taskId: string;
    showBtn: boolean = true;//拨打的状态\经办人是否是当前登录人的操作权限
    stepList: any = [];
    forbiddenDisplay: boolean = false;
    guaranteeModeOptions: any[];
    abusinesscode: any[] = [
        // { value: '010102', label: '存量营销' },
        // { value: '010103', label: '个性化营销' },
        { value: '010104', label: '枢纽直销' },
        { value: '010105', label: '信用卡直销' },
        { value: '010701', label: '批量预授信转电销' },
        // { value: '010201', label: '联系客户经理' },
        // { value: '010202', label: '联系支行主管' },
        // { value: '010301', label: '车易卡审批' },
        // { value: '010302', label: '永续合同审批' },
        // { value: '010401', label: '直销回访' },
        // { value: '010402', label: '直销催收' },
        // { value: '010501', label: '个性化自动营销' },
        // { value: '010502', label: '还款提醒' },
        // { value: '010503', label: '通用模板' },
        // { value: '010601', label: '其他' },
    ];
    atypecode: any[] = [
        { value: '0101', label: '营销类' },
        { value: '0107', label: '枢纽层' },
    ];
    constructor(
        public activatedRoute: ActivatedRoute,
        public teleMarketingHttpService: TeleMarketingHttpService,
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.MarketingCampaigntType = this.code['DXRASKTYPE'];//任务类型
        this.guaranteeModeOptions = this.code['GuaranteeMode'];//
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.urlParam = window.location.host;
    }

    ngOnInit() {
        let first = { label: '电销', value: '14' }
        this.MarketingCampaigntType.unshift(first);
        this.judgStatu();
        if (this.commfunc.getSessionDataCH('markFlag') == '2') {//已处理的数据\经办人是否是当前登录人
            this.showBtn = false;
        } else {//未处理
            this.showBtn = true;
        }
        this.dataList = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap);
        // this.taskId = JSON.parse(this.commfunc.getSessionDataCH('dxlc')).taskId;
        const params = {
            workEffortId: this.dataList.taskId,
            workEffortParentId: this.dataList.workEffortParentId,
            workEffortTypeId: this.dataList.workEffortTypeId
        };
        this.teleMarketingHttpService.queryCustCommunicationWorkList(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.returnList[0].createPartyId != this.userId) {//登录人是经办人时
                    this.showBtn = false;
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })

        this.queryClick();
        this.queryslice();
        debugger
        if (this.dataList.workEffortParentId == '01' || this.dataList.workEffortParentId == '20') {//直销(激活)
            this.queryLoanDetailInfo();
        } else if (this.dataList.workEffortParentId == '17' || this.dataList.workEffortParentId == '22') {//批量预授信转授信(激活)
            this.queryPreCreditDetailInfo();
        }
    }
    queryslice() {
        this.stepList = [];
        if (this.dataList.obCustomercode) {
            let param = {
                custNo: this.dataList.obCustomercode,//客户号
                // custType:,//客户类型:0-个人,1-企业
            }
            this.teleMarketingHttpService.queryCustContactNumbers(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.acctNoteNum) {
                        let acctNoteNums = data.acctNoteNum.split(',');
                        acctNoteNums.forEach(item => {
                            this.stepList.push({ label: item, value: item });
                        });
                    }
                    if (data.mobileNum && data.mobileNum != "***") {
                        this.stepList.push({ label: data.mobileNum, value: data.mobileNum });
                    }
                    if (data.telNum && data.telNum != "***") {
                        this.stepList.push({ label: data.telNum, value: data.telNum });
                    }
                    this.stepList = _.clone(this.stepList);
                    if (this.stepList.length == '0') {
                        if (this.dataList.obPhone && this.dataList.obPhone != "***") {
                            this.stepList.push({ label: this.dataList.obPhone, value: this.dataList.obPhone });
                        }
                    }
                    this.stepList = _.clone(this.stepList);
                    if (this.dataList.obPhone && this.dataList.obPhone != "***") {

                    } else {
                        this.dataList.obPhone = this.stepList[0].value;
                    }
                    this.dataList.obPhone = _.clone(this.dataList.obPhone);
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        } else {
            if (this.dataList.obPhone && this.dataList.obPhone != "***") {
                this.stepList.push({ label: this.dataList.obPhone, value: this.dataList.obPhone });
            }
        }
    }
    apply(num) {
        this.show = num;
    }
    //判断当前路由 改变样式active
    judgStatu() {
        let url = window.location.href;
        let urlSplit = url.split('/');
        urlSplit.map(v => {
            if (v === 'contact-record') {
                this.show = 1;
            }
            else if (v === 'contact-record-history') {
                this.show = 2;
            }
        })
    }
    //查询分机号
    queryClick() {
        let param = {
            loginNo: this.userId
            // loginNo: '1084'
        }
        this.teleMarketingHttpService.getSoftphoneInfo(param).subscribe(data => {
            if (data.returnCode.code == "success") {
                let param = {
                    ctiIp: data.ctiIp,
                    agentNo: data.agentNo,
                    aextention: data.aextention,
                    scallerno: data.scallerno
                }
                this.commfunc.setSessionDataCH('resultList', JSON.stringify(param));
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })

    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }

    login_call2() {
        let dataLists = JSON.parse(this.commfunc.getSessionDataCH('dxlc'));
        // let scallerno = '9';
        let scallerno = JSON.parse(this.commfunc.getSessionDataCH('resultList')).scallerno;
        dataLists.dataMap = JSON.parse(dataLists.dataMap);
        dataLists.dataMap.obPhone = this.dataList.obPhone;
        dataLists = _.clone(dataLists);
        dataLists.dataMap = JSON.stringify(dataLists.dataMap);
        dataLists = _.clone(dataLists);
        this.commfunc.setSessionDataCH('dxlc', JSON.stringify(dataLists));
        this.called = scallerno + this.dataList.obPhone;
        window['$caf']['phone'].open();
        window['$caf']['phone'].ready();
        window['$caf']['phone'].callout(this.called); //拨打
        this.call();
    }

    login_call(num) {
        if (num == '1') {//客户
            let obBussCode = '';
            switch (this.dataList.workEffortParentId) {//外拨业务类型
                case '01' || '20': obBussCode = '010104'; break;//枢纽直销
                case '16' || '21': obBussCode = '010105'; break;//信用卡直销 
                case '17' || '22': obBussCode = '010701'; break;//批量预授信
            }
            let param = {
                mobile: this.dataList.obPhone,
                obBussCode: obBussCode
            }
            this.teleMarketingHttpService.BZ_11003000105_02(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    if (data.flag && data.flag != '0') {//是-禁拨
                        // let MarketingCampaigntType = this.MarketingCampaigntType.filter((item) => {
                        //     return item.value == this.dataList.workEffortParentId
                        // })[0]['label'];
                        let atypecode = '';//业务类型
                        let index = 1;
                        if (data.atypecode) {
                            let arrTemp = data.atypecode.split(',');
                            arrTemp.forEach(element => {
                                this.atypecode.forEach(element2 => {
                                    if (element == element2.value) {
                                        if (index == 1) {
                                            atypecode = atypecode + element2.label;
                                        } else {
                                            atypecode = atypecode + ',' + element2.label;
                                        }
                                        index++;
                                    }
                                });
                            });
                        }

                        let abusinesscode = '';//外拨业务类型\回显
                        let index2 = 1;
                        if (data.abusinesscode) {
                            let arrTemp = data.abusinesscode.split(',');
                            arrTemp.forEach(element => {
                                this.abusinesscode.forEach(element2 => {
                                    if (element == element2.value) {
                                        if (index2 == 1) {
                                            abusinesscode = abusinesscode + element2.label;
                                        } else {
                                            abusinesscode = abusinesscode + ',' + element2.label;
                                        }
                                        index2++;
                                    }
                                });
                            });
                        }
                        this.confirmationService.confirm({
                            message: `
                                    <div class="ui-g-12">
                                        <div class="ui-g-12">
                                            <h4>该号码为禁拨，请确认</h4>
                                        </div>
                                        <div class="ui-g-4">
                                            客户姓名:<span>${this.dataList.obName}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            客户号:<span>${this.dataList.obCustomercode}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            手机号:<span>${this.dataList.obPhone}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            业务类型:<span>${atypecode}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            外拨业务:<span title="${abusinesscode}">${abusinesscode}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            生效日期:<span>${data.aenabledate}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            失效日期:<span>${data.adisabledate}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            创建人:<span>${data.aopername}</span>
                                        </div>
                                        <div class="ui-g-4">
                                            创建日期:<span>${data.tscreatetime}</span>
                                        </div>
                                    </div>
                                `,
                            accept: () => {
                                this.login_call2();
                            }
                        })
                    } else {
                        this.login_call2();
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }

            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }
    //点击拨打按钮是调接口获取拨打次数
    call() {
        let abusinesscode = '';
        if (this.dataList.workEffortParentId) {
            switch (this.dataList.workEffortParentId) {
                case '01' || '20': abusinesscode = '010104'; break;//枢纽直销
                case '16' || '21': abusinesscode = '010105'; break;//信用卡直销 
                case '17' || '22': abusinesscode = '010701'; break;//批量预授信
            }
        }
        let param = {
            taskId: this.dataList.taskId,
            abusinesscode: abusinesscode,
            flag: '2'
        }
        this.teleMarketingHttpService.addCustCommunication(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    callend() {
        window['$caf'].phone.callend();
        window['$caf'].phone.cancelwork();
        this.divstyle('loginout');
        window['$caf'].phone.loginout();
    }

    divstyle(type) {
        if (type == "login") {
            $('#logindiv').css('display', 'none');
            $('#callenddiv').css('display', 'none');
        } else if (type == "loginout") {
            $('#logindiv').css('display', 'block');
            $('#loginoutdiv').css('display', 'none');
            $('#callenddiv').css('display', 'none');
        } else if (type == "callouting") {
            $('#logindiv').css('display', 'none');
            $('#loginoutdiv').css('display', 'none');
            $('#callenddiv').css('display', 'block');
        } else if (type == "notlogin") {
            $('#logindiv').css('display', 'block');
        }
    }


    initCAF() {
        let AgentEvent_Hold_Type = '';
        window['$caf'].phone.config({
            // host: this.resultList.ctiIp,
            // port: 8000,
            // agentPwd: "cti-1234",
            // agentNo: this.resultList.agentNo,
            // phoneNumber: this.resultList.aextention,
            host: "10.1.97.27",
            port: 8000,
            agentPwd: "cti-1234",
            agentNo: "3001",
            phoneNumber: "4004"
        });

        window['$caf'].phone.listen(function (type, id, message) {
            if (id == "0") {
                this.divstyle("notlogin");
            } else if (id == "1") {
                alert("!!!!!");
                this.divstyle('login');
            } else if (id == "2") {
                this.divstyle('loginout');
            } else if (id == "5") {//挂机成功
                this.divstyle('loginout');
            } else if (id == "6") {
            } else if (id == "7") {//通话态单独处理,此处不做处理
            } else if (id == "8") {
                this.divstyle('rest');
            } else if (type == "callouting") {//外呼时间
                this.aconnectid = message;
                this.divstyle('callouting');
                this.startTime = this.commfunc.getFormatDate;//获取开始时间
            } else if (type == "AgentOther_PhoneAlerting") {
                this.callid = message;
                this.divstyle('AgentOther_PhoneAlerting');
            } else if (type == "AgentEvent_Auto_Answer") {
                this.divstyle('AgentEvent_Auto_Answer');
                this.clearClock();
                this.startClock();
            } else if (type == "AgentEvent_Hold") {
                if (AgentEvent_Hold_Type == '1') {//转保持
                } else if (AgentEvent_Hold_Type == '2') {//转咨询
                }
            } else if (type == "qxholdSuccess") {
            } else if (type == "stop_innerHelp_Success") {
            } else if (type == "confrence_Success") {
            } else if (type == "stop_confrence_Success") {
            } else if (type == "supervise_Success") {
            } else if (type == "qualitycontrol_Success") {
            } else if (type == "addinsert_Success") {
            } else if (type == "intercept_Success") {
            } else if (type == "anser_Success") {
            } else if (type == "Call_Out_Fail") {
                this.divstyle('loginout');
            } else if (type == 'callData') {
            } else if (type == 'setCallId') {
            } else if (type == 'cancelwork_fail') {
            } else if (type == 'restSuccess') {
            } else if (type == 'cancelrestSuccess') {
            } else if (type == 'AgentMediaEvent_Record') {      //获取录音文件
                let startindx = message.lastIndexOf('fileName=') + 9;
                let endindx = message.lastIndexOf(".V3") + 3;
                this.voice_path = message.substring(startindx, endindx); //录音文件名
            } else if (type == 'callendSuccess') {//挂机事件
                alert("!!!!!!!!!!!!");
                alert(this.aconnectid);//电话记录id
                alert(this.voice_path);//录音文件名



                this.endTime = this.commfunc.getFormatDate;//获取开始时间
                let dateDiff = this.endTime - this.startTime;//时间差的毫秒数
                let dayDiff = Math.floor(dateDiff / (24 * 3600 * 1000));//计算相差天数
                let leavel = dateDiff % (24 * 3600 * 1000);//计算天数后剩余的毫秒数
                let hours = Math.floor(leavel / (3600 * 1000));//计算出小时数
                //计算相差分钟数
                let leavel2 = leavel % (3600 * 1000);//计算小时数后剩余的毫秒数
                let minutes = Math.floor(leavel2 / (3600 * 1000));//计算相差分钟数
                //计算相差秒数
                let leavel3 = leavel2 % (60 * 1000);//计算分钟数后剩余的毫秒数
                let seconds = Math.round(leavel3 / 1000);
                this.dateTime = dayDiff + ':' + hours + ':' + minutes + ':' + seconds
                let param = {
                    aconnectid: this.aconnectid,
                    aucid: this.voice_path,
                    obTime: this.dateTime
                }
                let paramObj = JSON.stringify(param)
                this.commfunc.setSessionDataCH('tapeInfo', paramObj)
            }
        });
    }

    compileStr(cust) {
        return this.commfunc.compileStr(cust)
    }

    queryLoanDetailInfo() {
        let param = {
            custId: this.dataList.obCustomercode
        }
        this.teleMarketingHttpService.queryLoanDetailInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList['bussType'] = data.bussType;
                this.dataList['loanAmt'] = data.loanAmt;
                this.dataList['endDate'] = data.endDate;
                this.dataList['EcardCreditLmt'] = data.EcardCreditLmt;

                let dxlc = JSON.parse(this.commfunc.getSessionDataCH('dxlc'));
                dxlc['dataMap'] = JSON.stringify(this.dataList);
                this.commfunc.setSessionDataCH('dxlc', JSON.stringify(dxlc));
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    queryPreCreditDetailInfo() {
        let param = {
            workEffortId: this.dataList.taskId
        }
        this.teleMarketingHttpService.queryPreCreditDetailInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataList['groupId'] = data.groupId;//客户群号
                this.dataList['groupName'] = data.groupName;//客户群名称
                this.dataList['productName'] = data.productName;//产品名称
                this.dataList['guaranteeType'] = data.guaranteeType;//主担保方式
                this.dataList['endDate'] = data.endDate;//授信到期日期
                this.dataList['loanOfficerAdvises'] = data.loanOfficerAdvises;//信贷员建议额度(单位元)
                this.dataList['principalLinkMan'] = data.principalLinkMan;//主联系人
                this.dataList['principalLinkManOrg'] = data.principalLinkManOrg;//主联系人机构

                let dxlc = JSON.parse(this.commfunc.getSessionDataCH('dxlc'));
                dxlc['dataMap'] = JSON.stringify(this.dataList);
                this.commfunc.setSessionDataCH('dxlc', JSON.stringify(dxlc));
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

}
