import { Component, OnInit } from '@angular/core';
import { CALENDAR_CN, tableMessage } from '../../../../../@uisftech/common/constant/config';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { ActivatedRoute, Router } from '@angular/router';
import { OuterRaskBean } from './bean/outer-rask.bean';
import { Message } from 'primeng/components/common/message';
import { TeleMarketingHttpService } from '../http/telemarketing.http.service';
import { TZB_CUS_DOWNLOAD } from '../../../../../@uisftech/common/service/http.constant';
import { CustomeHttpService } from '../../../../../@uisftech/view/http/uisftech-view.http.service';
import * as _ from 'lodash';

@Component({
    selector: 'outer-rask',
    templateUrl: './outer-rask.component.html',
    styleUrls: ['./outer-rask.component.scss'],
    providers: [Commfunc, TeleMarketingHttpService]

})
//外拨业务列表查询-暂时还未定怎么做
export class OutRask implements OnInit {
    ch: any = CALENDAR_CN;
    code: any;
    TaskType: any = [];
    dataList: any = [];
    isPanduan: any = [];//判断是否
    AnotConnect: any = [];
    areSponse: any = [];
    first: number = 0;//分页控制
    startDate: Date = null;
    endDate: Date = null;
    outerRaskBean: OuterRaskBean = new OuterRaskBean();
    totalNum: number;
    msgs: Message[] = []; //提示信息
    TaskStatus: any = [];//任务状态
    isconnet: any = [];//是否接通
    flage: string;
    whFlag: string;
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    creditDate: Date = null;
    fromDate: Date = null;
    thruDate: Date = null;
    userId: string;
    littleClassName: string;
    bigClassNo: string;
    littleClassNo: string;
    areSponse2: any = [];
    paramForQueryUserDetail: any;
    raskbean: any;
    approve: any = [];
    custSource: any = [];
    isShowPage: boolean = false;
    bussType: string;
    bussTypes: string;
    workEffortParentIds: string;
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public teleMarketingHttpService: TeleMarketingHttpService,
        public customeHttpService: CustomeHttpService,
        public activatedRoute: ActivatedRoute,
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.TaskType = this.code['DXRASKTYPE'];//任务类型
        this.isPanduan = this.code['IsPanduan'];//判断是否
        this.isconnet = this.code['ISCONNET'];//电话是否接通
        this.AnotConnect = this.code['AnotConnec'];
        this.areSponse2 = this.code['AreSponse2'];
        this.TaskStatus = this.code['TASKSTATUS'];//任务状态
        this.approve = this.code['APPROVE'];//申请状态
        this.custSource = this.code['CUSTSOURCE'];//客户来源
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;

        // this.littleClassName = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).littleClassName;
        // this.bigClassNo = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).bigClassNo;
        // this.littleClassNo = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).littleClassNo;
        if (this.commfunc.getSessionDataCH('markFlag')) {
            this.commfunc.clearSession('markFlag');
        }
        if (this.commfunc.getSessionDataCH('taskId')) {
            this.commfunc.clearSession('taskId');
        }
        if (this.commfunc.getSessionDataCH('dxlc')) {
            this.commfunc.clearSession('dxlc');
        }
        if (this.commfunc.getSessionDataCH('tapeInfo')) {
            this.commfunc.clearSession('tapeInfo');//录音文件名
        }
        if (this.commfunc.getSessionDataCH('setDate')) {
            this.commfunc.clearSession('setDate');//流水号
        }
    }

    ngOnInit() {
        let paremse = {};
        this.teleMarketingHttpService.isCommunicationPost(paremse).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.paramMap.directSellingToElectricSelling == '1' && data.paramMap.custCommunicationPost == '1') {
                    this.TaskType.unshift({ label: "请选择", value: " " });
                    this.bussType = '';
                    this.outerRaskBean.workEffortTypeId = null;
                    this.bussTypes = '';
                    this.workEffortParentIds = null;
                } else if (data.paramMap.directSellingToElectricSelling == '0' && data.paramMap.custCommunicationPost == '0') {
                    this.TaskType.unshift({ label: "请选择", value: " " });
                    this.bussType = '';
                    this.outerRaskBean.workEffortTypeId = null;
                    this.bussTypes = '';
                    this.workEffortParentIds = null;
                } else {
                    if (data.paramMap.custCommunicationPost == '0') {
                        let TaskTypes = [];
                        this.TaskType.forEach(items => {
                            if (items.value == '16') {

                            } else {
                                TaskTypes.push(items);
                            }
                        });
                        this.TaskType = TaskTypes;
                        this.bussType = '01';
                        this.outerRaskBean.workEffortTypeId = '14';
                        this.bussTypes = '01';
                        this.workEffortParentIds = '14';
                    } else if (data.paramMap.custCommunicationPost == '1') {
                        let TaskTypes = [];
                        this.TaskType.forEach(items => {
                            if (items.value == '01') {

                            } else {
                                TaskTypes.push(items);
                            }
                        });
                        this.TaskType = TaskTypes;
                        this.bussType = '16';
                        this.outerRaskBean.workEffortTypeId = '14';
                        this.bussTypes = '16';
                        this.workEffortParentIds = '14';
                    }
                }
                this.bussType = _.clone(this.bussType);
                this.TaskType = _.clone(this.TaskType);
                this.CheckTask();
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
        if (this.activatedRoute.snapshot.queryParams['flag'] == '2') {
            this.whFlag = '2'; //历史
        } else {
            this.whFlag = '1'; //历史
        }
    }
    //校验是否电销后查询
    CheckTask() {
        if (this.commfunc.getSessionDataCH('raskBean')) {
            let parems = JSON.parse(this.commfunc.getSessionDataCH('raskBean'));
            parems['commonHeader'] = '';
            this.teleMarketingHttpService.queryCustCommunicationWorkList(parems).subscribe(data => {
                if (data.returnCode.code == "success") {
                    this.totalNum = data.totalCount;
                    this.dataList = data.returnList;
                    this.outerRaskBean = parems;
                    this.outerRaskBean.pageNum = parems.pageNum;
                    this.outerRaskBean.pageSize = parems.pageSize;
                    this.first = (parems.pageNum - 1) * parems.pageSize;
                    this.bussTypes = parems.workEffortParentId;
                    this.bussType = parems.workEffortParentId;
                    this.workEffortParentIds = parems.workEffortTypeId;
                    this.raskbean = this.outerRaskBean;
                    let parmeess = {
                        value: this.bussType
                    };
                    this.BussType(parmeess);
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
                this.commfunc.clearSession('raskBean');
            }, error => {
                this.commfunc.clearSession('raskBean');
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        } else {
            let parmeess = {
                value: this.bussType
            };
            this.BussType(parmeess);
            this.queryClick();
        }
    }

    BussType(event) {
        console.log(1);
        switch (event.value) {
            case '16':
                //信用卡直销
                this.isShowPage = true;
                break;
            default:
                this.isShowPage = false;

        }
        this.startDate = null;
        this.endDate = null;
        this.creditDate = null;
        this.fromDate = null;
        this.thruDate = null;
        this.outerRaskBean = new OuterRaskBean();
        // this.dataList=[];//切换把表格清空
        if (event.value) {
            if (event.value == '01') {
                this.outerRaskBean.workEffortTypeId = '14';
            } else if (event.value == '16') {
                this.outerRaskBean.workEffortTypeId = '14';
            } else {
                this.outerRaskBean.workEffortTypeId = null;
            }
        } else {
            this.outerRaskBean.workEffortTypeId = null;
        }
    }
    //查询按钮
    queryClick() {
        this.outerRaskBean.pageNum = 1;
        this.outerRaskBean.pageSize = 10;
        this.first = 0;
        this.query();
    }
    //查询
    query() {
        if (this.bussType) {
            this.outerRaskBean.workEffortParentId = this.bussType;
        } else {
            this.outerRaskBean.workEffortParentId = null;
        }
        this.outerRaskBean.endDate = this.commfunc.transDate(this.endDate);
        this.outerRaskBean.startDate = this.commfunc.transDate(this.startDate);
        this.outerRaskBean.creditDate = this.commfunc.transDateN(this.creditDate);
        this.outerRaskBean.fromDate = this.commfunc.transDateN(this.fromDate);
        this.outerRaskBean.thruDate = this.commfunc.transDateN(this.thruDate);
        this.teleMarketingHttpService.queryCustCommunicationWorkList(this.outerRaskBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.totalNum = data.totalCount;
                this.dataList = data.returnList;
                this.raskbean = this.outerRaskBean;
            } else {
                this.totalNum = 0;
                this.dataList = [];
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.totalNum = 0;
            this.dataList = [];
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //重置
    resetClick() {
        this.startDate = null;
        this.endDate = null;
        this.creditDate = null;
        this.fromDate = null;
        this.thruDate = null;
        this.outerRaskBean = new OuterRaskBean();
        this.querySecleReset();
    }
    //页码重置
    querySecleReset() {
        this.outerRaskBean.pageNum = 1;
        this.outerRaskBean.pageSize = 10;
        this.first = 0;
        this.bussType = this.bussTypes;
        this.outerRaskBean.workEffortTypeId = this.workEffortParentIds;
        this.query();
    }

    paginate　(event) {
        //每页显示的条数
        this.outerRaskBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        this.outerRaskBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.outerRaskBean.pageSize;
        this.query();
    }
    //开始日期
    showDataStart() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始时间不能大于结束时间' });
                setTimeout(() => {
                    this.startDate = null;
                })
            }
        }
    }
    //结束日期
    showDataEnd() {
        if (this.startDate && this.endDate) {
            if (this.startDate > this.endDate) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '开始时间不能大于结束时间' });
                setTimeout(() => {
                    this.endDate = null;
                })
            }
        }
    }
    //手动输入日期时，失去焦点时进行判断
    onchange1() {
        this.showDataStart()
    }
    onchange2() {
        this.showDataEnd()
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //电销详情
    toDetail(item) {
        console.log(item);
        if (item.taskStatus == '2') {//已处理要调历史中的接口
            let param = {
                isAllNode: false,
                itemName: this.littleClassName,
                itemNode: this.littleClassNo,
                itemType: this.bigClassNo,
                userId: this.userId,
                processId: item.processId,
            }
            this.customeHttpService.bpmQueryTaskOnlyList(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    if (data.pageData.result.length > 0) {
                        let paramItem = data.pageData.result[0].varMap.dataMap;
                        let param = {
                            taskId: data.pageData.result[0].taskId,
                            dataMap: JSON.stringify(paramItem),
                        }
                        this.commfunc.setSessionDataCH('raskBean', JSON.stringify(this.raskbean));
                        this.commfunc.setSessionDataCH('markFlag', '2');
                        this.commfunc.setSessionDataCH('dxlc', JSON.stringify(param));
                        this.router.navigate(['/pages/tzb/custom/teleMarketing/contact-record']);
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })

        } else {//未处理和待重拨
            //(待办任务捡入检出接口)
            let param = {
                workEffortId: item.workEffortId,
            }
            this.customeHttpService.queryWorkEffortDetailInfo(param).subscribe(data => {
                if (data.returnCode.code == "success") {
                    let workEffortParentIds = data.workEffortParentId;
                    if (data.currentStatusId == '1') {
                        let params = {
                            createPartyId: this.userId,
                            workEffortId: data.workEffortId,
                        }
                        this.customeHttpService.updateWorkEffortCommInfo(params).subscribe(data => {
                            if (data.returnCode.code == "success") {
                                let param = {
                                    processId: item.processId,
                                }
                                this.customeHttpService.bpmQueryUnDoTaskList(param).subscribe(data => {
                                    if (data.returnCode.code == "success") {
                                        if (data.pageData.result.length > 0) {
                                            if (data.pageData.result[0].varMap.dataMap.workEffortTypeId != workEffortParentIds) {
                                                data.pageData.result[0].varMap.dataMap.workEffortTypeId = workEffortParentIds;
                                            }
                                            let paramItem = data.pageData.result[0].varMap.dataMap;
                                            let param = {
                                                taskId: data.pageData.result[0].taskId,
                                                dataMap: JSON.stringify(paramItem),
                                            }
                                            this.commfunc.setSessionDataCH('raskBean', JSON.stringify(this.raskbean));
                                            this.commfunc.setSessionDataCH('markFlag', '1')
                                            this.commfunc.setSessionDataCH('dxlc', JSON.stringify(param));
                                            this.router.navigate(['/pages/tzb/custom/teleMarketing/contact-record']);
                                        }
                                    } else {
                                        this.msgs = [];
                                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                                    }
                                }, error => {
                                    this.msgs = [];
                                    this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                                })

                            } else {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                            }
                        }, error => {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                        })
                    } else {
                        if (data.createPartyId != this.userId) {
                            this.msgs = [];
                            this.msgs.push({ severity: 'info', summary: '提示', detail: '该任务已被领取!' });
                        } else {
                            //未处理和待重拨
                            let param = {
                                processId: item.processId,
                            }
                            this.customeHttpService.bpmQueryUnDoTaskList(param).subscribe(data => {
                                if (data.returnCode.code == "success") {
                                    if (data.pageData.result.length > 0) {
                                        if (data.pageData.result[0].varMap.dataMap.workEffortTypeId != workEffortParentIds) {
                                            data.pageData.result[0].varMap.dataMap.workEffortTypeId = workEffortParentIds;
                                        }
                                        let paramItem = data.pageData.result[0].varMap.dataMap;
                                        let param = {
                                            taskId: data.pageData.result[0].taskId,
                                            dataMap: JSON.stringify(paramItem),
                                        }
                                        this.commfunc.setSessionDataCH('raskBean', JSON.stringify(this.raskbean));
                                        this.commfunc.setSessionDataCH('markFlag', '1')
                                        this.commfunc.setSessionDataCH('dxlc', JSON.stringify(param));
                                        this.router.navigate(['/pages/tzb/custom/teleMarketing/contact-record']);
                                    }
                                } else {
                                    this.msgs = [];
                                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                                }
                            }, error => {
                                this.msgs = [];
                                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
                            })
                        }
                    }
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
            })
        }
    }
    //导出excel
    raskQuery() {
        if (this.bussType) {
            this.outerRaskBean.workEffortParentId = this.bussType;
        } else {
            this.outerRaskBean.workEffortParentId = null;
        }
        this.outerRaskBean.endDate = this.commfunc.transDate(this.endDate);
        this.outerRaskBean.startDate = this.commfunc.transDate(this.startDate);
        this.outerRaskBean.creditDate = this.commfunc.transDateN(this.creditDate);
        // this.outerRaskBean.workEffortParentId = this.bussType;
        this.teleMarketingHttpService.custCommunicationExport(this.outerRaskBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl, "_self");
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        })
    }
}
