import { Component, OnInit } from '@angular/core';
import { CALENDAR_CN, tableMessage } from '../../../../../@uisftech/common/constant/config';
import { TeleMarketingHttpService } from '../http/telemarketing.http.service';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { ContactRecordBean } from './bean/contact-record.bean';
import { Message } from 'primeng/primeng';
import { ActivatedRoute } from '@angular/router';
import * as _ from 'lodash';

@Component({
    selector: 'contact-record-history',
    templateUrl: './contact-record-history.component.html',
    styleUrls: ['./contact-record-history.component.scss'],
    providers: [TeleMarketingHttpService, Commfunc]
})
// 联络结果历史查询
export class ContactRecordHistory implements OnInit {
    ch: any = CALENDAR_CN;
    resultList: any = [];
    tabMesg: string = tableMessage;
    orgTreePage: boolean = false;
    CONTACTTYPE: any = [];
    ContactWay: any = [];
    MarketingCampaigntType: any = [];
    contactRecordBean: ContactRecordBean = new ContactRecordBean();
    table_list: any = [];
    code: any;
    msgs: Message[] = []; //提示信息
    obDate: Date = null;
    inValue: any;
    dataMapList: any = {};
    // dataMapLists: any = {};
    typeCantact: any = [];
    WayCanct: any = [];
    markFlag: string;
    taskId: string;
    constructor(
        public teleMarketingHttpService: TeleMarketingHttpService,
        public commfunc: Commfunc,
        public activatedRoute: ActivatedRoute
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.MarketingCampaigntType = this.code['DXRASKTYPE'];//任务类型
        this.CONTACTTYPE = [{ label: '请选择', value: '' }];
        this.ContactWay = [{ label: '请选择', value: '' }];
    }

    ngOnInit() {
        this.dataMapList = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap);
        // this.dataMapLists = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('raskBean')).dataMap);
        this.contactRecordBean.bussType = this.dataMapList.workEffortParentId;
        // if (this.commfunc.getSessionDataCH('markFlag') == '2') {
        //     this.markFlag = this.commfunc.getSessionDataCH('markFlag')
        //     this.commfunc.setSessionDataCH('markFlag', this.markFlag);
        // }
        this.queryHistoryCommunicationEventResultList();
        this.cantactClick();//查询联系类型码值
        // this.cantactWay();
        // this.queryClick();
        let paremse = {};
        this.teleMarketingHttpService.isCommunicationPost(paremse).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (data.paramMap.directSellingToElectricSelling == '1' && data.paramMap.custCommunicationPost == '1') {
                    this.MarketingCampaigntType.unshift({ label: "请选择", value: " " });
                } else if (data.paramMap.directSellingToElectricSelling == '0' && data.paramMap.custCommunicationPost == '0') {
                    this.MarketingCampaigntType.unshift({ label: "请选择", value: " " });
                } else {
                    if (data.paramMap.custCommunicationPost == '0') {
                        let TaskTypes = [];
                        this.MarketingCampaigntType.forEach(items => {
                            if (items.value == '16') {

                            } else {
                                TaskTypes.push(items);
                            }
                        });
                        this.MarketingCampaigntType = TaskTypes;
                    } else if (data.paramMap.custCommunicationPost == '1') {
                        let TaskTypes = [];
                        this.MarketingCampaigntType.forEach(items => {
                            if (items.value == '01') {

                            } else {
                                TaskTypes.push(items);
                            }
                        });
                        this.MarketingCampaigntType = TaskTypes;
                    }
                }
                this.MarketingCampaigntType = _.clone(this.MarketingCampaigntType);
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })
    }
    //查询
    queryClick() {
        this.contactRecordBean.taskId = this.dataMapList.taskId;
        this.contactRecordBean.obDate = this.commfunc.transDateN(this.obDate);
        this.contactRecordBean.obCustomercode = this.dataMapList.obCustomercode;
        this.teleMarketingHttpService.queryHistoryCommunicationEventResultDetail(this.contactRecordBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.table_list = data.returnList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })

    }

    //详情
    toDetail(item) {
        console.log(item);
        this.orgTreePage = true;
        this.inValue = item;
    }
    //联系类型
    cantactClick() {
        let params = {
            pageNum: 1,
            pageSize: 99999999
        }
        this.teleMarketingHttpService.queryContactMechTypeList(params).subscribe(data => {
            let arr = data.contactMechTypeList;
            this.typeCantact = [{ label: '请选择', value: '' }];
            if (arr) {
                arr.forEach(item => {
                    this.typeCantact.push({ label: item.description, value: item.contactMechTypeId });
                });
            }
        })
    }
    //联系方式是根据联系类型进行判断显示
    contactBtn(event) {
        if (event.value == '') {
            this.contactRecordBean.contactWay = '';
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择联系类型' });
            return;
        }
        let params = {
            pageNum: 1,
            pageSize: 99999999,
            contactMechTypeId: event.value
        }
        this.teleMarketingHttpService.queryCommunicationEventTypeList(params).subscribe(data => {
            let arr = data.communicationEventTypeList;
            this.WayCanct = [{ label: '请选择', value: '' }];
            if (arr) {
                arr.forEach(item => {
                    this.WayCanct.push({ label: item.description, value: item.communicationEventTypeId });

                });
            }
        })
    }

    queryHistoryCommunicationEventResultList() {
        this.contactRecordBean['workEffortId'] = this.dataMapList.taskId;
        this.teleMarketingHttpService.queryHistoryCommunicationEventResultList(this.contactRecordBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let hash1 = {}, hash2 = {};
                if (data.resultList && data.resultList.length != 0) {
                    data.resultList.forEach(element => {
                        this.CONTACTTYPE.push({ label: element.contactTypeDescription, value: element.contactType });
                        this.ContactWay.push({ label: element.contactWayDescription, value: element.contactWay });
                    });
                    //json数组去重
                    this.CONTACTTYPE = this.CONTACTTYPE.reduce((item, next) => {
                        hash1[next.value] ? '' : hash1[next.value] = true && item.push(next);
                        return item
                    }, []);
                    this.ContactWay = this.ContactWay.reduce((item, next) => {
                        hash1[next.value] ? '' : hash1[next.value] = true && item.push(next);
                        return item
                    }, []);
                }
                this.table_list = data.resultList;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //重置
    recler() {
        this.contactRecordBean = new ContactRecordBean;
        this.obDate = null;
        this.contactRecordBean.bussType = this.dataMapList.workEffortTypeId;
        this.contactRecordBean.taskId = this.dataMapList.taskId;
        this.contactRecordBean.obCustomercode = this.dataMapList.obCustomercode;
    }
}
