import { Component, OnInit, ViewChild, AfterViewInit, Inject, forwardRef, OnDestroy } from '@angular/core';
import { TeleMarketingHttpService } from '../http/telemarketing.http.service';
import { TeleMarketingBean } from '../bean/telemarketing.bean';
import { Message, ConfirmationService } from 'primeng/primeng';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { ActivatedRoute, Router } from '@angular/router';
import { ContactRecordDeploy } from './contact-record-module/contact-record-module.component';
import * as $ from 'jquery';
import { TeleMarketingComponent } from '../telemarketing.component';
import { AppproResultBean } from '../../../customCenter/customer-distribution/bean/business-information.bean';
import { BussinessMap } from 'app/pages/tzb/exam-appr-comp/custom/custom-group/bean/AppproResult.bean';
import * as moment from 'moment';
@Component({
    selector: 'contact-record',
    templateUrl: './contact-record.component.html',
    styleUrls: ['./contact-record.component.scss'],
    providers: [TeleMarketingComponent, TeleMarketingHttpService, Commfunc, ContactRecordDeploy, ConfirmationService]
})
// 联络结果记录
export class ContactRecord implements OnInit, OnDestroy {

    @ViewChild('ContactRecordDeploy') contactRecordDeploy: ContactRecordDeploy;
    custRequestNo: any;
    creditType: any;
    businessType: any;
    iseleconsig: boolean = false;
    showBtns = false;
    seqno: any;
    isqianyuezhihui: boolean;
    agreementId: any;
    signStatus: any;
    bpmalldata: any;
    teleMarketingBean: TeleMarketingBean = new TeleMarketingBean();
    appproResultBean: AppproResultBean = new AppproResultBean()
    // teleMarketingBean: any = {};
    msgs: Message[] = [];
    code: any;
    ContactType: any = [];
    custBasic: any = {};
    dataList: any;
    CONTACTTYPE: any = [];
    ContactWay: any = [];
    inValue: any;
    index: string;
    userName: string;
    userId: string;
    tapeInfo: any = {};
    ex: any = [];
    aconnectid: string;
    bussinessMap: BussinessMap = new BussinessMap();
    setDate: any = {};
    obDate: Date = new Date();
    taskId: string;
    dataTask: string;
    markFlag: string;
    showBtn: boolean = false;
    isClickYL: boolean = false;//可点
    // isClickYL1:boolean=true;//可点
    littleClassName: string;
    bigClassNo: string;
    littleClassNo: string;
    Historyflag: string;
    bigClassName: string;
    obPhone: string;
    reasonDisplay: boolean = false;
    stopReason: string;
    dataList2: any;
    isTelemarketingList: boolean = false;//经办人是否是当前登录人
    constructor(
        public teleMarketingHttpService: TeleMarketingHttpService,
        public commfunc: Commfunc,
        public activatedRoute: ActivatedRoute,
        public router: Router,
        public confirmationService: ConfirmationService,
        // @Inject(forwardRef(() => TeleMarketingComponent)) public app: TeleMarketingComponent
    ) {
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;
        this.userName = JSON.parse(this.commfunc.getSessionDataCH('chName')).userName;
        this.bpmalldata = JSON.parse(this.commfunc.getSessionDataCH('bpmalldata'));
        this.dataList2 = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap);
        if (this.commfunc.getSessionDataCH('bpmalldata')) {
            const params = {
                'workEffortId': this.bpmalldata.varMap.dataMap.workEffortIdParent,
            };
            this.teleMarketingHttpService.queryCreditData(params).subscribe(data => {
                if (data.returnCode.code === 'success') {
                    this.businessType = data.creditMap.businessType;
                    this.creditType = data.creditMap.creditType;
                    this.custRequestNo = data.creditMap.custRequestNo;
                    this.dzqy();
                    if (this.businessType == '01' && this.creditType == '0001') {
                        this.showBtns = true;
                    }
                } else {
                    this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
                }
            });
        }
        // if (this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')) {
        //     this.littleClassName = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).littleClassName;
        //     this.bigClassNo = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).bigClassNo;
        //     this.littleClassNo = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).littleClassNo;
        //     this.bigClassName = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).bigClassName;
        //     this.Historyflag = JSON.parse(this.commfunc.getSessionDataCH('bpmQueryTaskOnlyList')).Historyflag;
        // }
    }

    ngOnInit() {
        if (this.commfunc.getSessionDataCH('markFlag') == '2') {//已处理的数据
            this.markFlag = this.commfunc.getSessionDataCH('markFlag')
            // this.showBtn = false;
            if (JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap) {
                this.dataList = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap);
                if (this.dataList.obDate && this.dataList.obTime) {
                    this.teleMarketingBean.obDate = this.dataList.obDate + '  ' + this.dataList.obTime;
                } else {
                    this.teleMarketingBean.obDate = this.dataList.obDate
                }
            }
            if (this.dataList.contactType) {
                //联系类型
                let params = {
                    pageNum: 1,
                    pageSize: 99999999,
                    contactMechTypeId: this.dataList.contactType,
                }
                this.teleMarketingHttpService.queryContactMechTypeList(params).subscribe(data => {
                    let arr1 = data.contactMechTypeList;
                    if (arr1) {
                        this.CONTACTTYPE.push({ label: arr1[0].description, value: arr1[0].contactMechTypeId });
                        this.teleMarketingBean.contactType = arr1[0].contactMechTypeId;
                    }
                })
            }
            if (this.dataList.contactWay) {
                //联系方式
                let param = {
                    pageNum: 1,
                    pageSize: 99999999,
                    communicationEventTypeId: this.dataList.contactWay,
                }
                this.teleMarketingHttpService.queryCommunicationEventTypeList(param).subscribe(data => {
                    let arr = data.communicationEventTypeList;
                    if (arr) {
                        this.ContactWay.push({ label: arr[0].description, value: arr[0].communicationEventTypeId });
                        this.teleMarketingBean.contactWay = arr[0].communicationEventTypeId
                    }
                })
            }
        } else {//未处理
            if (this.obDate) {
                this.teleMarketingBean.obDate = this.commfunc.transDateN(this.obDate)
            }
            this.cantactClick();
            if (JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap) {
                this.dataList = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap)
                this.taskId = JSON.parse(this.commfunc.getSessionDataCH('dxlc')).taskId;
            }
            // this.showBtn = true;
        }

        const params = {
            workEffortId: this.dataList.taskId,
            workEffortParentId: this.dataList.workEffortParentId,
            workEffortTypeId: this.dataList.workEffortTypeId
        };
        this.teleMarketingHttpService.queryCustCommunicationWorkList(params).subscribe(data => {
            if (data.returnCode.code === 'success') {
                if (data.returnList[0].createPartyId == this.userId) {//登录人是经办人时
                    this.showBtn = true;
                }
                if (data.returnList[0].taskStatus) {//未处理
                    let markFlag;
                    switch (data.returnList[0].taskStatus) {
                        case '1' || '12'://已处理\中止
                            markFlag = 2;
                        default: markFlag = 1;
                    }
                    this.commfunc.setSessionDataCH('markFlag', markFlag);

                    this.contactRecordDeploy.ngOnInit();
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        })

        this.obPhoneWatcher.run();//初始化
    }
    outValue(event) {
        debugger
        console.log(this.dataList)
        console.log(event)
        this.teleMarketingBean = $.extend(event, this.dataList, this.teleMarketingBean);//合并对象
        this.bussinessMap['dataMap'] = this.teleMarketingBean;
        console.log(this.dataList)
        this.subMintBtn();
    }

    //提交
    sub(num) {
        this.inValue = { showflag: '1' };
        this.index = num;
    }
    eleconsig() {
        if (this.businessType == '01' && this.creditType == '0001') {
            this.confirmationService.confirm({//分配
                message: '该客户进行电子签约?',
                accept: () => {
                    const pamse = {
                        'custRequestNo': this.custRequestNo,
                    };
                    this.teleMarketingHttpService.creationContractDitel(pamse).subscribe(data => {
                        if (data.returnCode.code === 'success') {
                            this.dzqy();
                            this.msgs = [];
                            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        } else {
                            this.msgs = [];
                            this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
                        }
                    }, error => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
                    })
                }
            });
        }
    }
    dzqy() {
        const pams = {
            custRequestNo: this.custRequestNo,
        }
        this.teleMarketingHttpService.selectContractDitel(pams).subscribe(data => {
            if (data.returnCode.code === 'success') {
                this.signStatus = data.infoList[0].signStatus;
                this.agreementId = data.infoList[0].agreementId;
                this.seqno = data.infoList[0].seqNo;
                if (this.signStatus == '02' || this.signStatus == '01' || this.signStatus == '03') {
                    this.iseleconsig = true;
                } else if (this.signStatus == '04' || this.signStatus == '') {
                    this.iseleconsig = false;
                }
            } else {
                this.msgs = [{ severity: 'error', summary: '错误:', detail: data.returnCode.message }];
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        });
    }
    subMintBtn() {
        if (this.teleMarketingBean.acall == '1' && this.teleMarketingBean.aisconnect == '1') {//外呼为是，接通为是
            if (!this.teleMarketingBean.aresponse) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '客户响应不能为空' });
                return;
            }
        }
        if (this.teleMarketingBean.acall == '1' && this.teleMarketingBean.aisconnect == '2') {//外呼为是，接通为否
            if (!this.teleMarketingBean.anotconnect) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '无法接通原因说明不能为空' });
                return;
            }
        }
        if (this.teleMarketingBean.acall == '0') {//外呼为否
            if (!this.teleMarketingBean.aresponse) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '客户响应不能为空' });
                return;
            }
        }
        this.tapeInfo = JSON.parse(this.commfunc.getSessionDataCH('tapeInfo'));//录音文件名
        this.setDate = JSON.parse(this.commfunc.getSessionDataCH('setDate'));//流水号
        if (this.setDate) {
            this.teleMarketingBean.aconnectid = this.setDate.aconnectid;//流水号
            // this.teleMarketingBean.obDate = this.setDate.obDate;
        }
        if (this.tapeInfo) {
            this.teleMarketingBean.aucid = this.tapeInfo.aucid;//录音文件名
        }
        console.log(this.tapeInfo)
        this.teleMarketingBean.oboperid = this.userId;
        this.teleMarketingBean.oboperName = this.userName;
        let obTime = moment(new Date()).format('HH:mm:ss');
        this.teleMarketingBean.obTime = obTime;
        if (this.index == '1') {//提交
            // this.isClickYL = true;
            this.teleMarketingBean.flag = '1';
            this.addCustBtn();
        } else if (this.index == '0') {
            this.teleMarketingBean.flag = '0';
            this.confirmationService.confirm({
                message: '确认要重拨吗',
                accept: () => {
                    // this.isClickYL = true;//重拨
                    this.addCustBtn();
                },
            });

        }
    }
    //重拨和提交的接口
    addCustBtn() {
        this.dataList = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap);
        this.teleMarketingBean.obPhone = this.dataList.obPhone;
        let abusinesscode = '';
        if (this.dataList.workEffortParentId) {
            switch (this.dataList.workEffortParentId) {
                case '01' || '20': abusinesscode = '010104'; break;//枢纽直销
                case '16' || '21': abusinesscode = '010105'; break;//信用卡直销 
                case '17' || '22': abusinesscode = '010701'; break;//批量预授信
            }
        }
        this.teleMarketingBean['abusinesscode'] = abusinesscode;
        this.teleMarketingHttpService.addCustCommunication(this.teleMarketingBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (this.index == '1') {
                    this.bpmNext();//是提交的时候调next接口
                } else {
                    // this.isClickYL = false;//重拨
                    this.commfunc.clearSession('tapeInfo');//录音文件名
                    this.commfunc.clearSession('setDate');//流水号
                    this.router.navigate(['/pages/tzb/custom/teleMarketing/telemarketing-list', { bussType: this.dataList.workEffortParentId }]);
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                // this.isClickYL = false;
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }

        }, error => {
            // this.isClickYL = false;
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //联系类型
    cantactClick() {
        let params = {
            pageNum: 1,
            pageSize: 99999999
        }
        this.teleMarketingHttpService.queryContactMechTypeList(params).subscribe(data => {
            let arr = data.contactMechTypeList;
            this.CONTACTTYPE = [{ label: '请选择', value: '' }];
            if (arr) {
                arr.forEach(item => {
                    this.CONTACTTYPE.push({ label: item.description, value: item.contactMechTypeId });

                });
            }
        })
    }

    //联系方式是根据联系类型进行判断显示
    contactBtn(event) {
        if (event.value == '') {
            this.teleMarketingBean.contactWay = '';
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '请先选择联系类型' });
            return;
        }
        let params = {
            pageNum: 1,
            pageSize: 99999999,
            contactMechTypeId: event.value
        }
        this.teleMarketingHttpService.queryCommunicationEventTypeList(params).subscribe(data => {
            let arr = data.communicationEventTypeList;
            this.ContactWay = [{ label: '请选择', value: '' }];
            if (arr) {
                arr.forEach(item => {
                    this.ContactWay.push({ label: item.description, value: item.communicationEventTypeId });

                });
            }
        })
    }

    //next 接口
    bpmNext() {
        this.appproResultBean.bussinessMap = this.bussinessMap;
        this.appproResultBean.userId = this.userId;
        this.appproResultBean.taskId = this.taskId;
        this.teleMarketingHttpService.bpmNextTask(this.appproResultBean).subscribe(data => {
            let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (data.returnCode.code == 'success') {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                // this.isClickYL = false;
                this.commfunc.clearSession('markFlag');
                this.commfunc.clearSession('tapeInfo');//录音文件名
                this.commfunc.clearSession('setDate');//流水号
                this.returnClick();//返回上一页
                // window.history.go(-1)
                // this.router.navigate(['/pages/tzb/custom/teleMarketing/outer-rask']);
            } else {
                this.ex = [];
                let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
                if (s.extParam.specifyNextOpers) {
                    delete s.extParam.specifyNextOpers
                    let commonHeader = s;
                    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                // this.isClickYL = false;
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
            }
        }, error => {
            this.ex = [];
            let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
            if (s.extParam.specifyNextOpers) {
                delete s.extParam.specifyNextOpers
                let commonHeader = s;
                this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
            }
            // this.isClickYL = false;
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败' }];
        })
    }
    //点击返回
    returnClick() {
        /**
        * 从任务池跳转过来的
        */
        if (this.commfunc.getSessionDataCH('taskpooltype') && this.commfunc.getSessionDataCH('taskpooltype').toString() == 'false') {
            this.router.navigate(['/pages/tzb/custom/reminder-do/backlog-detail/btn-page9/task-pool'], { queryParams: { flag: '5', totalNum: '10' } });
        } else {
            console.log(this.activatedRoute.snapshot.params);
            this.router.navigate(['/pages/tzb/custom/teleMarketing/telemarketing-list', this.activatedRoute.snapshot.params]);
        }
    }
    //中止
    stop() {
        this.reasonDisplay = true;
    }
    stop2() {
        if (!this.stopReason) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '中止原因必输' });
            return
        }
        let params = {
            workEffortId: this.dataList.taskId,
            reason: this.stopReason
        }
        this.teleMarketingHttpService.terminateCommunication(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.reasonDisplay = false;
                setTimeout(() => {
                    this.router.navigate(['/pages/tzb/custom/teleMarketing/telemarketing-list']);
                }, 500);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }

        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //客户号码的监听事件
    obPhoneWatcher = {
        parent: this,
        run() {
            this.init();
            this.watch();
        },//监听的方法
        init() {
            this.phoneNumber = this.current();
        },//当前页面的值
        current() {
            let ret = '';
            let dataMap = JSON.parse(JSON.parse(this.parent.commfunc.getSessionDataCH('dxlc')).dataMap);
            ret = dataMap.obPhone;
            return ret;
        },
        phoneNumber: '',
        handle: null,
        watch() {
            this.handle = setInterval(() => {
                let current = this.current();//改变的值
                if (current != this.phoneNumber) {//不相同时，赋予子页面改变的值
                    this.outputData();
                    this.phoneNumber = current;
                }
            }, 50);
        },
        outputData() {
            this.parent.dataList.obPhone = this.current();//赋予子页面改变的值
        },
        stopWatching() {
            if (this.handle) {
                clearInterval(this.handle);//消除监听方法
            }
        }
    }

    ngOnDestroy(): void {//退出此页面时运行
        this.obPhoneWatcher.stopWatching();
    }
}
