import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { TeleMarketingComponent } from './telemarketing.component';
import { ContactRecord } from './contact-record/contact-record.component';
import { ContactRecordHistory } from './contact-record-history/contact-record-history.component';
import { OutRask } from './outer-rask/outer-rask.component';
import { TelemarketingList } from 'app/pages/tzb/custom/telemarketing/telemarketing-list/telemarketing-list.component';

export const routes: Routes = [
	{
		path: '',
		component: TeleMarketingComponent,
		children: [
			{ path: '', redirectTo: 'contact-record', pathMatch: 'full' },
			{
				path: 'contact-record',
				component: ContactRecord
			},
			{
				path: 'contact-record-history',
				component: ContactRecordHistory,
			},
		]
	},
	{
		path: 'outer-rask',
		component: OutRask,
	},
	{
		path: 'telemarketing-list',
		component: TelemarketingList,
	},
]

export const TeleMarketingRouting: ModuleWithProviders = RouterModule.forChild(routes);
